import React, {memo, useCallback, useRef} from 'react';
import {
  Fonts,
  IC_MORE,
  stripSpecialCharacters,
  styled,
  translate,
  useTheme,
  useUser,
} from '@true/core';
import {useMasterVersionDoctypes} from '@policy/store/masterVersionDoctypes';
import moment from 'moment';
import {useCollections} from '@policy/store/collections';
import {useDoctypes} from '@policy/store/doctypes';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {DoctypeMoreMenu} from '@policy/screens/DocumentTypesScreen/DoctypeMoreMenu';
import {navigateToEditMasterVersion} from '@policy/utils';
import {UIButton} from '@true/ui';

interface Props {
  index?: number;
  id: number;
  isLast?: boolean;
}

export const MasterDoctypeItem = memo((props: Props) => {
  const {id} = props;

  const masterVersion = useMasterVersionDoctypes(id);
  const user = useUser(masterVersion?.user_id);
  const collection = useCollections(masterVersion?.collection_id);
  const doctype = useDoctypes(masterVersion?.doctype_id);
  const theme = useTheme();

  const ref = useRef<BottomSheetModal | null>(null);

  const onEditPress = useCallback(() => {
    navigateToEditMasterVersion({id});
  }, [id]);

  return (
    <SContainer onPress={onEditPress}>
      <STitleWrapper>
        <STitleView>
          <STitle numberOfLines={2}>
            {stripSpecialCharacters(masterVersion?.name)}
          </STitle>
        </STitleView>

        <SButton
          imageSize={24}
          source={IC_MORE}
          imageStyle={{tintColor: theme.neutral2}}
          onPress={() => ref.current?.present()}
        />
      </STitleWrapper>
      <SItemWrapper>
        <SubItem label={translate('policy.created_by')} value={user?.name} />
        <SubItem
          label={translate('policy.date')}
          value={moment(masterVersion?.since, 'X').format('DD/MM/YYYY')}
        />

        <SubItem label={translate('policy.type')} value={doctype?.name} />
        <SubItem
          label={translate('policy.collection')}
          value={collection?.name}
        />
      </SItemWrapper>

      <DoctypeMoreMenu ref={ref} id={id} />
    </SContainer>
  );
});

const SContainer = styled.TouchableOpacity`
  padding: 8px 16px;
  background-color: ${p => p.theme.backgroundColor};
  margin-top: 8px;
`;
const STitleWrapper = styled.View`
  flex-direction: row;
`;

const SItemWrapper = styled.View``;

const STitleView = styled.View`
  flex: 1;
`;

const STitle = styled.Text`
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Medium};
  font-size: 16px;
  line-height: 24px;
`;

const SButton = styled(UIButton)``;

const SubItem = memo(
  ({
    label = '',
    value = '',
  }: {
    label: string | undefined;
    value: string | undefined;
  }) => {
    return (
      <SSubItemContainer>
        <SSubTitle>{label}:</SSubTitle>
        <SSubView>
          <SSubValue numberOfLines={2}>{value}</SSubValue>
        </SSubView>
      </SSubItemContainer>
    );
  },
);

const SSubItemContainer = styled.View`
  flex-direction: row;
  padding-top: 8px;
`;

const SSubView = styled.View`
  flex: 1;
`;

const SSubTitle = styled.Text`
  color: ${p => p.theme.neutral2};
  font-size: 12px;
  line-height: 16px;
  margin-right: 4px;
  width: 90px;
`;

const SSubValue = styled.Text`
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Medium};
  font-size: 12px;
  line-height: 16px;
`;
