import {DynamicHeader, ScreenWrapper, ScrollableTabView} from '@true/ui';
import React, {memo, useCallback, useState} from 'react';
import {Fonts, stripSpecialCharacters, styled, translate, useNavigationParams} from '@true/core';
import {useDoctypes} from '@policy/store/doctypes';
import {PoliciesItem} from '@policy/screens/HomeScreen/PoliciesItem';
import {requestPolicyByDoctype} from '@policy/store/policies/functions';
import {requestMasterVersionDoctype} from '@policy/store/masterVersionDoctypes/functions';
import {getPoliciesByQuery, usePoliciesByQuery} from '@policy/store/policies';
import {SharedValue, useDerivedValue, useSharedValue,} from 'react-native-reanimated';
import {MasterDoctypeItem} from '@policy/screens/DocumentTypesScreen/MasterDoctypeItem';
import {getMasterVersionDoctypesByQuery, useMasterVersionDoctypesByQuery,} from '@policy/store/masterVersionDoctypes';
import {navigateToDoctypeFilter} from '@policy/utils';
import {FilterIcon} from '@policy/components/FilterIcon';
import {SelectedItemProvider, useSelectedItem,} from '@policy/context/SelectedItemContext';
import {SelectedHeader} from '@policy/screens/HomeScreen/SelectedHeader';
import {IDocTypes} from '@policy/store/doctypes/types';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';

export interface DoctypeDetailScreenProps {
  id: string | number;
}

export interface IParams {
  collection?: number | string;
  storage?: number | string;
}

export const DEFAULT_PARAMS = {
  collection: undefined,
  storage: undefined,
};

export const DoctypeDetailScreen = memo(() => {
  const {id} = useNavigationParams<DoctypeDetailScreenProps>();

  const doctype = useDoctypes(id);

  const [params, setParams] = useState<IParams>(DEFAULT_PARAMS);

  const renderItem = useCallback(
    (id: number, index?: number, isLast?: boolean) => {
      return <PoliciesItem id={id} isLast={isLast} index={index} />;
    },
    [],
  );
  const renderItem2 = useCallback(
    (id: number, index?: number, isLast?: boolean) => {
      return <MasterDoctypeItem id={id} isLast={isLast} index={index} />;
    },
    [],
  );

  const animatedCollapseIndex = useSharedValue(0);

  const animatedHeight = useSharedValue(0);
  const animatedTop = useSharedValue(0);

  return (
    <SelectedItemProvider>
      <ScreenWrapper isGrey={true}>
        <DynamicHeader
          hideSeparator={true}
          title={stripSpecialCharacters(doctype?.name || '')}
          rightComponent={
            <SHeaderWrapper>
              <FilterIcon
                params={params}
                onPress={() => {
                  navigateToDoctypeFilter({
                    params,
                    setParams,
                  });
                }}
              />
            </SHeaderWrapper>
          }
          animatedCollapseIndex={animatedCollapseIndex}
        />
        <SViewContent>
          <SelectedHeader
            title={stripSpecialCharacters(doctype?.name || '')}
            animatedHeight={animatedHeight}
            animatedTop={animatedTop}
          />
          <_Comp
            animatedCollapseIndex={animatedCollapseIndex}
            params={params}
            id={id}
            renderItem={renderItem}
            renderItem2={renderItem2}
            animatedHeight={animatedHeight}
            doctype={doctype}
          />
        </SViewContent>
      </ScreenWrapper>
    </SelectedItemProvider>
  );
});

const _Comp = memo(
  ({
    animatedCollapseIndex,
    params,
    id,
    renderItem,
    renderItem2,
    doctype,
    animatedHeight,
  }: {
    animatedCollapseIndex: SharedValue<number>;
    params: IParams;
    id: string | number;
    renderItem: any;
    renderItem2: any;
    doctype?: IDocTypes;
    animatedHeight: SharedValue<number>;
  }) => {
    const {selectedState, selectedIds} = useSelectedItem();

    const scrollEnabled = useDerivedValue(() => selectedIds.value.length == 0);

    return (
      <ScrollableTabView.Provider
        scrollEnabled={scrollEnabled}
        disabledLoadData={selectedState}
        onLayout={layout => {
          'worklet';
          animatedHeight.value = layout.headerHeight;
        }}
        onCollapse={index => {
          'worklet';
          animatedCollapseIndex.value = index;
        }}
        tabs={[
          {
            title: translate('policy.all_documents'),
            content: p => (
              <ScrollableTabView.List
                index={p.index}
                params={{id, ...params}}
                requestFn={requestPolicyByDoctype}
                getItemByQuery={getPoliciesByQuery}
                useItemByQuery={usePoliciesByQuery}
                query={'doctype'}
                renderItem={renderItem}
              />
            ),
          },
          {
            title: translate('policy.master_versions'),
            content: p => (
              <ScrollableTabView.List
                index={p.index}
                params={{id}}
                requestFn={requestMasterVersionDoctype}
                getItemByQuery={getMasterVersionDoctypesByQuery}
                useItemByQuery={useMasterVersionDoctypesByQuery}
                renderItem={renderItem2}
              />
            ),
          },
        ]}>
        <ScrollableTabView.CollapsableTabBar>
          <STitleWrapper>
            <STitle numberOfLines={2}>
              {stripSpecialCharacters(doctype?.name || '')}
            </STitle>
          </STitleWrapper>
        </ScrollableTabView.CollapsableTabBar>
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    );
  },
);

const SHeaderWrapper = styled.View`
  margin-right: 16px;
`;

const SViewContent = styled.View`
  background-color: ${p => p.theme.neutral9};
  flex: 1;
`;

const STitleWrapper = styled.View`
  padding-left: 16px;
  background-color: ${p => p.theme.backgroundColor};
`;
const STitle = styled.Text`
  font-size: 20px;
  line-height: 28px;
  font-family: ${Fonts.SemiBold};
  color: ${p => p.theme.neutral1};
`;
