import React, {RefObject, useCallback} from 'react';
import {MyBottomSheet, useAnimatedSnapPoints} from '@true/ui';
import {BottomSheetModal, BottomSheetView} from '@gorhom/bottom-sheet';
import {
  Colors,
  interactManager,
  memoForwardRef,
  translate,
  useAsyncFn,
  useAutoToastError,
  useBoolean,
  useTheme,
} from '@true/core';
import {MenuItem} from '@policy/components/MenuItem';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';
import {navigateToEditMasterVersion} from '@policy/utils';
import {requestRemoveMasterVersion} from '@policy/store/masterVersionDoctypes/functions';
import {MyConfirmationDialog} from '@policy/components/Dialog/MyConfirmationDialog';

export const DoctypeMoreMenu = memoForwardRef(
  (props: {id: number}, ref: RefObject<BottomSheetModal>) => {
    const {onViewLayout, animatedSnapPoints} = useAnimatedSnapPoints();
    const [visible, show, hide] = useBoolean();

    const theme = useTheme();

    const [{loading: removeLoading, error: removeError}, onRemove] = useAsyncFn(
      async () => await requestRemoveMasterVersion({id: props.id}),
      [props.id],
    );

    const onTrashPress = useCallback(() => {
      show();
    }, []);

    const onEditPress = useCallback(() => {
      interactManager(() => navigateToEditMasterVersion({id: props.id}), 300);
      ref.current?.dismiss();
    }, [props.id]);

    useAutoToastError(removeError);

    return (
      <MyBottomSheet
        name={'doctypes'}
        ref={ref}
        snapPoints={animatedSnapPoints}>
        <BottomSheetView onLayout={onViewLayout('ok')}>
          <MenuItem title={translate('policy.edit')} onPress={onEditPress} />
          <MenuItem
            textColor={Colors.red1}
            title={translate('policy.remove')}
            onPress={onTrashPress}
          />
          <AnimatedBottomSpace />
        </BottomSheetView>
        {/*<ConfirmationDialog*/}
        {/*  isVisible={visible}*/}
        {/*  onCloseRequest={hide}*/}
        {/*  title={translate('policy.remove_master_version')}*/}
        {/*  content={translate(*/}
        {/*    'policy.are_you_sure_you_want_to_remove_this_master_version',*/}
        {/*  )}*/}
        {/*  buttons={[*/}
        {/*    {text: translate('policy.cancel')},*/}
        {/*    {*/}
        {/*      text: translate('policy.confirm'),*/}
        {/*      onPress: onRemove,*/}
        {/*      loading: removeLoading,*/}
        {/*      backgroundColor: theme.primaryColor,*/}
        {/*      color: Colors.white,*/}
        {/*    },*/}
        {/*  ]}*/}
        {/*/>*/}
        <MyConfirmationDialog
          isVisible={visible}
          onCloseRequest={hide}
          title={translate('policy.remove_master_version')}
          content={translate(
            'policy.are_you_sure_you_want_to_remove_this_master_version',
          )}
          onPressRightButton={onRemove}
          colorLeftButton={theme.neutral02}
          bgColorRightButton={theme.red1}
          titleRightButton={translate('policy.remove')}
        />
      </MyBottomSheet>
    );
  },
);
